export default {
	components:{navbar},
	data(){ 
		return{
			titleName:'余额充值',
			payList:[
				{text:'微信支付',img:'../../static/recharge_icon1.png'},
				{text:'支付宝支付',img:'../../static/recharge_icon2.png'},
				{text:'银联支付',img:'../../static/recharge_icon3.png'},
			],
			current: 4,
			tab:0,
			show:false,
			paypsw:false,
			cardList:[
				{text:'农业'},{text:'建设'},{text:'某某'}
